import mtFooter from "../../components/footer.vue";
export default {
	components: {
		mtFooter
	},
	data: function(){
		return {
			pageLoad: false,
			pageHide: false,
			pageData: {}
		}
	},
	onLoad: function (option) {
		uni.setNavigationBarTitle({
			title: "个人中心",
		})
		this.getPage();
	},
	onShow: function () {
		if (this.pageHide) {
			this.pageHide = false;
			this.getPage();
		}
	},
	onHide: function () {
		this.pageHide = true;
	},
	methods: {
		gourl: function (url) {
			uni.navigateTo({
				url: url,
			})
		},
		getPage: function () {
			var that = this;
			uni.request({
				url: that.app.apiHost + "?m=user&ajax=1",
				data: {
					authcode: that.app.getAuthCode(),
					fromapp:that.app.fromapp()
				},
				success: function (res) {
					if (res.data.error == 1000) {
						/*
						uni.navigateTo({
							url: "../login/index",
						})
						*/
					} else {
						that.pageLoad = true;
						that.pageData = res.data.data;
					}


				}
			})
		}
	},
}
